import React, { Component } from 'react';

import Api from 'api';
import classNames from 'classnames';
import * as Tool from 'tool';
import * as Common from 'tool/common';
import * as getUserIdInfo from 'tool/getUserId';
import { BaiduMap } from 'tool/map';
import { inject, observer } from 'mobx-react';
import styles from "./business-detail.scss";

import StarRating from 'components/business/star-rating';
import ContactWay from 'components/business/contact-way';


@inject('Toast', 'ComNoContent', 'Header')
@observer
export default class BusinessDetail extends Component {
  constructor (props){
    super(props);
  }
  state = {
    query: Tool.getUrlParamQuery(),
    businessDetail: {},
    reviewList: {},
    staffsList:[], // 
    isContactWayWrapperShow: false,
    lng: '',
    lat: '',
    getBestIMSalesUrl: '', // 
    isShowAllActivitys: false,
    isShowAllPackages: false,
    distanceTips: '',
    dprnVersion: (Tool.getCookie('dprnversion') || '').replace(/\./g, ''),
    isWz: Tool.webApp.checkViolatApp() || Tool.getCookie('iswz'),
    isAutoApp: Tool.webApp.checkMainApp(), // 
    // 
    isBreakDownWxProgram: false
  }

  componentDidMount() {
    this.props.Header.$setTitle('');
    Tool.setTitle('');

    const {query} = this.state;
    // 
    if (query.isFaultMini){
      Tool.setCookie('isFaultMini', query.isFaultMini);
    }
    // 
    this.setState({
      isBreakDownWxProgram: Tool.webApp.isBreakDownWxProgram()
    });

    this.pvShow();
    this.realtimelocation();
    this.ininFetch();
    this.getReviewListFetch();
    this.getDealerStaffsFetch();
    this.getDealerStaffsListFetch();
  }

  // pv
  pvShow = () => {
    let { userId } = getUserIdInfo.getUserId();
    Common.trackCustomPageInit({
      'pageId': 'tmfy_by_dealer_detail_pv',
      'site_id': 1211120,
      'category_id': 1595,
      'sub_category_id': 18473
    }, {
      'userId': userId
    });
  }

  ininFetch = () => {
    const { query } = this.state;
    this.props.ComNoContent.$comConntent('');
    Api.getDealerDetail({
      dealerId: query.dealerId || 45619
    }).then((res) => {
      if (res.returncode === 0) {
        this.setState({
          businessDetail: res.result
        });
      } else {
        return Promise.reject(res);
      }
    }).catch(error => {
      // 
      this.setState({
        businessDetail: {}
      });
      // 
      this.props.ComNoContent.$comConntent(error.message || '');
      console.log(error, 'error:getDealerDetail');
    });
  }
  // 
  getReviewListFetch = () => {
    const { query } = this.state;
    Api.getReviewList({
      dealerId: query.dealerId || 40087
    }).then((res) => {
      this.setState({
        reviewList: res.result || {}
      });
    }); 
  }
  // 
  getDealerStaffsFetch = () => {
    const { query } = this.state;
    Api.getDealerStaffs({
      dealerIds: query.oldDealerId
    }).then((res) => {
      if (Number(res.returncode) === 0) {
        this.setState({
          getBestIMSalesUrl: res.result[0].imUrl || ''
        });
      } else {
        return Promise.reject(res);
      }
    }).catch(error => {
      console.log(error, 'error:getDealerStaffsFetch');
    });
  }
  // 
  getDealerStaffsListFetch = () => {
    const { query } = this.state;
    Api.getDealerStaffsList({
      dealerIds: query.oldDealerId,
      roleIds: '8,10'
    }).then((res) => {
      if (res.returncode === 0) {
        this.setState({
          staffsList: res.result || []
        });
      } else {
        return Promise.reject(res);
      }
    }).catch(error => {
      console.log(error, 'error:getDealerStaffsListFetch');
    });
  }
  // 
  realtimelocation = () => {
    // eslint-disable-next-line
    require('jsBridge');
    if (Tool.webApp.checkMainApp()) {
      window.jsBridgeV2.realtimelocation({
        success: (result) => {
          const resultLocal = result.result;
          Tool.setCookie2('yczj_longitude', resultLocal.BDLongitude);
          Tool.setCookie2('yczj_latitude', resultLocal.BDLatitude);
          this.setState({
            lng: resultLocal.BDLongitude,
            lat: resultLocal.BDLatitude
          }, () => {
            // this.getDistanceTime();
          });
        },
        fail: (result) => {
          console.log(JSON.stringify(result));
        }
      });
    }
  }
  // 
  getDistanceTime = () => {
    // TODO: react
    setTimeout(() => {
      const { query } = this.state;
      let { latitude, longitude } = this.state.businessDetail;
      // let { lng = 39.97756646760634, lat = 116.3352173394245 } = this.state;
      let lng = 39.97756646760634;
      let lat = 116.3352173394245;
      let that = this;
      BaiduMap().then(BMap => {
        window.BMap = BMap;
        let map = new window.BMap.Map('allmap');
        let userLatitude = '';
        let userLongitude = '';
        // 
        // 
        userLatitude = lat || query.wzlat || query.lat || query.latitude || Tool.getCookie('yczj_latitude');
        userLongitude = lng || query.wzlng || query.lng || query.longitude || Tool.getCookie('yczj_longitude');
        let userP1 = new window.BMap.Point(userLongitude, userLatitude);
        let dealerP2 = new window.BMap.Point(longitude, latitude);
        map.centerAndZoom(new window.BMap.Point(userLongitude, userLatitude), 12);
        let output = '';
        let searchComplete = function (results) {
          let plan = results.getPlan(0);
          // console.log(plan, 'plan-34234');
          // output = `${plan.getDistance(true).replace('', 'km')}${plan.getDuration(true)}`;
        };
        let transit = new window.BMap.DrivingRoute(map, {
          renderOptions: {
            map: map,
            autoViewport: true
          },
          onSearchComplete: searchComplete,
          onPolylinesSet: function() {
            console.log(output, 'output---');
            // that.setState({
            //   distanceTips: output
            // });
          }
        });
        transit.search(userP1, dealerP2);
      });
    }, 400); 
  }
  // 
  linkToVideoList = () => {
    const { query } = this.state;
    let dealerId = query.dealerId;
    this.handleTrackCustomEvent('tmfy_by_dealer_detail_logo_click', {
      objectId: dealerId
    });
    window.jsBridgeV2.openNativePageAsyncResult({
      url: `dianpinginside://cfwsmallvideolist?dealerid=${dealerId}`,
      requestCode: 1122,
      success: (result) => {
        console.log(JSON.stringify(result));
      },
      fail: (result) => {
        console.log(JSON.stringify(result));
      }
    });
  }
  // 
  handleBannerImg = (event, data, flag = '') => {
    const { query } = this.state;
    event.stopPropagation();
    event.cancelBubble = true;
    this.handleTrackCustomEvent('tmfy_by_dealer_detail_logo_click', {
      objectId: query.dealerId
    });
    if (flag === 'img') {
      let path = `${Common.getRemarkHostUrl()}/remark/store-img?dealerId=${query.dealerId || 40087}`;
      Common.pageJumpHandel(path);
    } else {
      if (data.vrUrl) {
        Common.pageJumpHandel(data.vrUrl);
        return;
      }
    }
  }
  // 
  openMap = (dealerParams) => {
    let { isWz, isAutoApp, dprnVersion, query } = this.state;
    let { latitude, longitude, address } = dealerParams;
    this.handleTrackCustomEvent('tmfy_by_dealer_detail_address_click', {
      objectId: query.dealerId
    });
    if (isWz) {
      let lgData = Tool.getCookie('lg_data');
      let mapUrl = `wzsdk://map?dlat=${latitude}&dlng=${longitude}&daddress=${address}`;
      if (parseInt(Tool.getCookie('issupportmap')) === 1) {
        // app 1  1
        window.location.href = mapUrl;
      }
      if (lgData && parseInt(JSON.parse(lgData)).issupportmap === 1) {
        //  ios 5.0 
        window.location.href = mapUrl;
      }
    } else if (isAutoApp) {
      // RN
      if (dprnVersion >= 200) {
        // RN
        const RN_MAP_VIEW_URL = `rn://DianPing_Main/MapView?name=${encodeURIComponent(dealerParams.name)}&address=${encodeURIComponent(address)}&lat=${latitude}&lng=${longitude}`;
        Tool.openNativePage(`autohome://rninsidebrowser?url=${encodeURIComponent(RN_MAP_VIEW_URL)}`);
        return false;
      }

      // 
      let linkUrl = `https://yczj.m.autohome.com.cn/topic/m/baidumap/index.html?destLng=${longitude}&destLat=${latitude}`;
      let OPEN_LINK_URL = encodeURIComponent(linkUrl);
      setTimeout(() => {
        Tool.openNativePage(`autohome://insidebrowser?url=${OPEN_LINK_URL}`);
      }, 10);
    }
  }
  // 
  handleDealerTel = () => {
    const { query } = this.state;
    const { phone = [] } = this.state.businessDetail;
    this.handleTrackCustomEvent('tmfy_by_dealer_detail_telephone_click', {
      objectId: query.dealerId
    });
    if (phone.length) {
      if (phone.length > 1) {
        this.setState({
          isContactWayWrapperShow: true
        });
      } else {
        window.location.href = `tel: ${phone[0]}`;
      }
    }
  }
  // 
  contactWayClose = () => {
    this.setState({
      isContactWayWrapperShow: false
    });
  }
  // 
  handleIconIm = () => {
    let { getBestIMSalesUrl, query } = this.state;
    this.handleTrackCustomEvent('tmfy_by_dealer_detail_im_click', {
      objectId: query.dealerId
    });
    Common.pageJumpHandel(`${getBestIMSalesUrl}&pvareaid=6838751`);
  }
  // 
  handleCommentMoreBtn = () => {
    const { query } = this.state;
    let path = `${Common.getRemarkHostUrl()}/remark/commentlist?dealerId=${query.dealerId}&serviceType=-1&storeType=1searchType=all`;
    Common.pageJumpHandel(path);
  }
  // 
  handleShowAllPackages = () => {
    const { query } = this.state;
    this.handleTrackCustomEvent('tmfy_by_dealer_detail_maintain_area_more_click', {
      objectId: query.dealerId
    });
    this.setState({
      isShowAllPackages: !this.state.isShowAllPackages
    });
  }
  // 
  handleShowAllActivitys = () => {
    const { query } = this.state;
    this.handleTrackCustomEvent('tmfy_by_dealer_detail_maintain_activity_more_click', {
      objectId: query.dealerId
    });
    this.setState({
      isShowAllActivitys: !this.state.isShowAllActivitys
    });
  }
  // 
  handleStoreTechnician = (data) => {
    this.handleTrackCustomEvent('tmfy_by_dealer_detail_technician_im_click', {
      objectId: data.salesId
    });
    Common.pageJumpHandel(`${data.imUrl}&pvareaid=6838753`);
  }
  // 
  handleOrderBtn = () => {
    const { query } = this.state;
    this.handleTrackCustomEvent('tmfy_by_dealer_detail_book_click', {
      objectId: query.dealerId
    });
    Common.pageJumpHandel('reservationDetail', { 
      type: query.type,
      selectDate: query.selectDate, // 
      itemIds: query.itemIds || '',
      dealerId: query.dealerId,
      oldDealerId: query.oldDealerId,
      plateNumber: query.plateNumber || '',
      brandId: query.brandId,
      seriesId: query.seriesId,
      specId: query.specId,
      travelDistance: query.travelDistance || 0
    });
  }
  // 
  handlePackages = (data) => {
    const { query } = this.state;
    this.handleTrackCustomEvent('tmfy_by_dealer_detail_get_maintain_click', {
      objectId: data.comboId
    });
    Common.pageJumpHandel('serviceDetail', {
      comboId: data.comboId, // id
      dealerId: data.dealerId,
      oldDealerId: data.oldDealerId || '', // id
      plateNumber: query.plateNumber || '', // 
      brandId: query.brandId || '', // id
      seriesId: query.seriesId || '', //id
      specId: query.specId || '', // id
      brandName: encodeURIComponent(encodeURIComponent(query.brandName)), // 
      seriesName: encodeURIComponent(encodeURIComponent(query.seriesName)), // 
      specName: encodeURIComponent(encodeURIComponent( query.specName)), // 
      travelDistance: query.travelDistance || '', // 
      selectDate: query.selectDate || ''// 
    });
  }
  // 
  imgVideoAmount = (item) => {
    return ((item.videos ? item.videos.length : 0) + (item.smallImages ? item.smallImages.length : 0));
  }
  // 
  showVideoPlayer = (params) => {
    let that = this;
    Common.showVideoPlayer(that, params);
  }
   // 
  showGallery = (index, originImages) => {
    let that = this;
    Common.showGallery(that, index, originImages);
  }

  // 
  handleReciveDiscount = (data) => {
    //  http://youche-h5.yz.test.autohome.com.cn
    // :https://youche.m.autohome.com.cn
    // https://youche.m.autohome.com.cn/app/mt/as/detail/{id}?pvareaid={pvreaid}&orderfrom={orderfrom}
    // orderfrom:   0(),1(m),2(app),3(),4(pc),5()
    this.handleTrackCustomEvent('tmfy_by_dealer_detail_get_activity_click', {
      objectId: data.activityId
    });
    const currentHost = window.location.host;
    let hostName = '';
    if (currentHost.slice(20, 24) === 'test') {
      hostName = `http://youche-h5.yz.test.autohome.com.cn`;
    } else {
      hostName = `https://youche.m.autohome.com.cn`;
    }
    let url = `${hostName}/app/mt/as/detail/${data.activityId}?orderfrom=2`;
    Common.pageJumpHandel(url);
  }

  //  
  handleScore = () => {
    const { query } = this.state;
    this.handleTrackCustomEvent('tmfy_by_dealer_detail_service_score_click', {
      objectId: query.dealerId
    });
    Common.pageJumpHandel('serviceInfo');
  }

  // 
  handleTrackCustomEvent(action, { objectId = '' } = {}) {
    let { userId } = getUserIdInfo.getUserId();
    // eslint-disable-next-line
    trackCustomEvent('yc_page_common_click', {
      'bus': '1',
      'refpage': Tool.getCookie('refpage') || '',
      'channel': Tool.getCookie('channel'),
      'city': Tool.getCookie('cityId') || 110100,
      'action': action
    }, {
      'userId': userId,
      'objectId': objectId
    });
  }

  render() {
    const {
      businessDetail,
      isContactWayWrapperShow,
      isShowAllPackages,
      isShowAllActivitys,
      getBestIMSalesUrl,
      reviewList,
      staffsList,
      isBreakDownWxProgram
    } = this.state;
    return (
      'dealerId' in businessDetail ? (
        <div className={styles.business_detail}>
          <div className={styles.business_header}>
            {/* banner */}
            { 
              'headImage' in businessDetail && (
                <div className={styles.banner_item}>
                  <div className={styles.img_cell} onClick={(e) => this.handleBannerImg(e, businessDetail)}>
                    <img src={businessDetail.headImage} alt=""/>
                    { businessDetail.vrUrl && (<div className={styles.vr_mask}>
                      <div className={classNames(styles.img, styles.flipInY)}>
                        <img src="//x.autoimg.cn/cfw/yc/images/wKgHIFr-Z86AE54jAAAVlowDaek886.png" />
                      </div>
                    </div>) }
                  </div>
                  <div className={styles.video_img}>
                    {
                      businessDetail.videoCount ? ( <span onClick={(e) => this.linkToVideoList(e)}>
                        { businessDetail.videoCount >= 100 ? '99+' : '' + businessDetail.videoCount }
                      </span>) : ''
                    }
                    {
                      businessDetail.imageCount ? ( <span onClick={(e) => this.handleBannerImg(e, businessDetail, 'img')}>
                        { businessDetail.imageCount >= 100 ? '99+' : '' + businessDetail.imageCount }
                      </span>) : ''
                    }
                  </div>
                </div>
              )
            }
            {/* /end banner */}
            {/*  */}
            <section className={styles.dealer_item}>
              <p className={styles.name}>
                {
                  !businessDetail.type && 
                  (<i className={classNames(styles.tag, styles.tag_4s)}>
                  </i>)
                }
                {/* <i className={classNames(styles.tag_service, styles.tag)}>
                  
                </i> */}
                { businessDetail.name }
              </p>
              <div className={styles.star_type}>
                <div className={styles.star_item}>
                  <StarRating width={ (businessDetail.score / 5) * 100 + '%' } className={styles.star}></StarRating>
                  { businessDetail.totalReviews && (`(${businessDetail.totalReviews})`) }
                </div>   
                {
                  businessDetail.labels && businessDetail.labels.length > 0 && (<p className={styles.types_item}>
                    {
                      businessDetail.labels.map((item, index) => (
                        <span key={index}>{item.labelName}{item.score}&nbsp;&nbsp;&nbsp;&nbsp;</span>
                      ))
                    }
                  </p>)
                }
                <span className={styles.order_btn} onClick={this.handleOrderBtn}></span>
              </div>
              
              {
                businessDetail.categorys && businessDetail.categorys.length > 0 && (
                  <p className={styles.service_type}>
                    {
                      businessDetail.categorys.map((item, index) => (
                        <span key={index}>
                          {item}
                        </span>
                      ))
                    }
                  </p>
                )
              }
              {
                businessDetail.address && (
                  <div className={styles.address_cell}>
                    <div className={styles.address_item} onClick={() => this.openMap(businessDetail)}>
                      <p className={styles.address} >
                        <i className={styles.icon_location2}>
                        </i>
                        <span>{ businessDetail.address }</span>
                      </p>
                      {/* <p className={styles.tips} >
                        xxxxxxxkm,1xxxxx
                      </p> */}
                    </div>
                    {
                      'phone' in businessDetail && (
                        <span  className={styles.tel} onClick={this.handleDealerTel}>
                          <i className={styles.icon_tel}></i>
                        </span>
                      )
                    }
                  </div>
                )
              }
            </section>
            {/* /end  */}
          </div>
          {/*  */}
          {
            businessDetail.transparentsrvInfo && Number(businessDetail.transparentsrvInfo.score) > 0 &&  (
              <div className={classNames(styles.score_cell, styles.item_cell)} onClick={this.handleScore}>
                <div className={styles.cell_hd}>
                  <i className={classNames(styles.tag, styles.tag_tou)}></i>
                </div>
                <div className={styles.cell_bd}>
                  <p className={styles.txt}></p>
                  <span className={styles.nums}>{ businessDetail.transparentsrvInfo.score.toFixed(1) }</span>
                </div>
                <div className={styles.cell_fd}>
                  <i className={styles.icon_arrow_r}></i>
                </div>
              </div>
            )
          }
          {/* /end  */}
          {/*  */}
          {
            <div className={styles.comment_content_wrap}>
              <div className={styles.title}>
                <span className={styles.txt}>
                  
                </span>
                {
                  'id' in reviewList ? (
                    <span className={styles.more_btn} onClick={this.handleCommentMoreBtn}>
                      
                    </span>
                  ) : null
                }
              </div>
              {
                reviewList.id ? (
                  <>
                    <div className={styles.user_info_wrap} >
                      <div className={styles.user_portrait}>
                        {
                          reviewList.headImage ? (<img src={reviewList.headImage} alt=""/>) : (<img src="//x.autoimg.cn/cfw/yc/images/autoapp-user-icon.png" alt=""/>)
                        }
                      </div>
                      <div className={styles.user_name_wrap}>
                        <div className={styles.user_name} >
                          <span className={styles.name}>{reviewList.userName}</span>
                        </div>
                        <p className={styles.release_times} >
                          <span>{Tool.formatTimeByReleaseTime(reviewList.reviewTimeStamp)}</span>
                        </p>
                      </div>
                    </div>
                    <div className={styles.score_wrap}>
                      <p></p>
                      <StarRating width={reviewList.scorePercent}></StarRating>
                      {
                        'cost' in reviewList && (
                          <div className={styles.fees_wrap} ><span>{ reviewList.cost }</span></div>
                        )
                      }
                    </div>
                    <div className={styles.comment_content}>
                      {
                        reviewList.content
                      }
                    </div>
                    {
                      reviewList.serviceItems && reviewList.serviceItems.length > 0 && (
                        <div className={classNames(styles.service_type, styles.common_wrap)} >
                          :&nbsp;&nbsp;
                          {
                            reviewList.serviceItems.map((item, index) => (
                              <span key={index}>
                                { item } &nbsp;
                              </span>
                            ))
                          }
                        </div>
                      )
                    }
                    {
                      reviewList.carSeriesName && (
                        <div className={classNames(styles.car_name, styles.common_wrap)} >:&nbsp;&nbsp;<span>{ reviewList.carSeriesName }</span></div>
                      )
                    }
                    {/* one_img_video_wrap two_img_video_wrap  three_img_video_wrap */}
                    <div
                      className={classNames({[styles.comment_images_wrap]: true, [styles.one_img_video_wrap]: this.imgVideoAmount(reviewList) === 1, [styles.two_img_video_wrap]: this.imgVideoAmount(reviewList) === 2, [styles.three_img_video_wrap]: this.imgVideoAmount(reviewList) >= 3})}>
                        {
                            reviewList.videos && reviewList.videos.length > 0 &&  (
                              reviewList.videos.map((item, index) => (
                              <div
                                key={index}
                                onClick={() => this.showVideoPlayer(reviewList)}
                                className={styles.video_wrap}>
                                <img src={item.coverImg} alt=""/>
                                <i className={styles.play_icon_wrap}></i>
                              </div>
                              ))
                            )
                        }
                        {
                          reviewList.videos && reviewList.videos.length > 0 && reviewList.smallImages && reviewList.smallImages.length > 0 && (
                            reviewList.smallImages.slice(0, 2).map((item, index) => (
                              <div key={index} 
                                  onClick={() => this.showGallery(index, reviewList.fullImages)}>
                                <img src={item} alt="" />
                              </div>
                            ))
                          )
                        }
                        {
                          reviewList.videos && reviewList.videos.length <= 0 && reviewList.smallImages && reviewList.smallImages.length > 0 && (
                            reviewList.smallImages.slice(0, 3).map((item, index) => (
                              <div key={index}
                                  onClick={() => this.showGallery(index, reviewList.fullImages)}>
                                <img src={item} alt="" />
                              </div>
                            ))
                          )
                        }
                    </div>
                  </>
                ) : (
                  <>
                    <div className={styles.no_comment}></div>
                  </>
                )
              }
            </div>
          }
          {/* /end  */}

          {/*  */}
          {
            businessDetail.packages && businessDetail.packages.length > 0 && (
              <div className={classNames(styles.service_item, styles.baoyang_item)}>
                <p className={styles.title}>
                  
                </p>
                {
                  <div className={classNames({[styles.list_item]: true, [styles.default_h]: !isShowAllPackages})} >
                  {
                      businessDetail.packages.map((item, index) => (
                      
                        <div className={styles.list_cell} key={index}>
                          <div className={styles.cell_hd}>
                            <p>{item.title}</p>
                          </div>
                          <div className={styles.cell_bd} >
                            <a  className={styles.receive_btn} onClick={() => this.handlePackages(item)}>
                              
                            </a>
                          </div>
                        </div>
                      ))
                  }
                  </div>
                }
                {
                  businessDetail.packages.length > 2 && (
                    <div className={styles.more_btn} onClick={this.handleShowAllPackages}>
                      {
                          isShowAllPackages ? '' : ''
                      } <i className={classNames({[styles.icon_more]: true, [styles.icon_more_up]:!isShowAllPackages })}></i>
                    </div>
                  )
                }
              </div>
            )
          }
          {/* /end  */}
          {/*  */}
          {
            businessDetail.activitys && businessDetail.activitys.length > 0 && (
              <div className={classNames(styles.service_item, styles.discount_item)}>
                <p className={styles.title}>
                  
                </p>
                {
                  <div className={classNames({[styles.list_item]: true, [styles.default_h]: !isShowAllActivitys})} >
                    {
                      businessDetail.activitys.map((item, index) => (
                        <div className={styles.list_cell} key={index}>
                          <div className={styles.cell_hd} >
                            <p>{item.asTitle}</p>
                          </div>
                          <div className={styles.cell_bd}>
                            <a className={styles.receive_btn} onClick={() => this.handleReciveDiscount(item)}>
                              
                            </a>
                          </div>
                        </div>
                        ))
                    }
                  </div>
                }
                {
                  businessDetail.activitys.length > 2 && (
                    <div className={styles.more_btn} onClick={this.handleShowAllActivitys}>
                      {
                        isShowAllActivitys ? '' : ''
                      }  <i className={classNames({[styles.icon_more]: true, [styles.icon_more_up]:!isShowAllActivitys })}></i>
                    </div>
                  )
                }
              </div>
            )
          }
          {/* /end  */}

          {/*  */}
          {
            staffsList && staffsList.length > 0 && (
              <div className={styles.technician_item}>
                <div className={styles.title}>
                  <span className={styles.txt}>
                    
                  </span>
                </div>
                <ul className={styles.list_item}>
                  {
                    staffsList.map((item, index) => (
                      item.list && item.list.length > 0 && item.list.map((subItem, subIndex) => {
                        if (isBreakDownWxProgram) {
                          return (<li key={subIndex}>
                            <div className={styles.avatar}>
                              <img src={subItem.photoUrl} />
                            </div>
                            <p className={styles.level}>{subItem.roleName}</p>
                            <p className={styles.name}>{subItem.salesName}</p>
                          </li>);
                        } else {
                          return (<li key={subIndex}>
                            <div className={styles.avatar}>
                              <img src={subItem.photoUrl} />
                            </div>
                            <p className={styles.level}>{subItem.roleName}</p>
                            <p className={styles.name}>{subItem.salesName}</p>
                            <span className={styles.advice_btn} onClick={() => this.handleStoreTechnician(subItem)}></span>
                          </li>);
                        }
                      })
                    ))
                  }
                </ul>
              </div>
            )
          }
          {/* /end  */}
          {
            businessDetail.phone && businessDetail.phone.length > 1 && <ContactWay
              ref='contactWayWrapper'
              isContactWayWrapperShow={isContactWayWrapperShow}
              phoneNumArr={businessDetail.phone}
              contactWayClose={this.contactWayClose}
            />
          }

          {/*  */}
          {
            getBestIMSalesUrl && !isBreakDownWxProgram && (<div className={styles.icon_im} onClick={this.handleIconIm}></div>)
          }
          
          {/*  */}
          <div className="map-container">
            <div id="allmap" style={{ width: '100%', height: '100%' }}></div>
          </div>
        </div>
      ) : null
    );
  }
}